@use 'base/variables' as v;
@use 'base/mixins' as m;

.filtros {
    background-color: v.$blanco;
    margin-top: 2rem;
    border-radius: 1rem;
    padding: 2rem;

    .filtros-inputs {
        @include m.tablet {
            display: flex;
            align-items: center;
            gap: 2rem;
        }
    }

    h2 {
        color: v.$indigo;
        align-items: center;
        @include m.tablet {
            margin: 0;
        }
    }
    
    .campo {
        margin-bottom: 2rem;
        display: flex;
        gap: 1rem;
    
        @include m.tablet {
            margin: 0;
        }
    }

    input[type="radio"] {
        accent-color: v.$indigo;
        width: 2rem;
        height: 2rem;
    }
}
